<template>
  <div class="d-flex flex-column min-vh-100">
  <h1 class="text-center text-white p-5 text-capitalize">Simpel calculator made with <span style="color: #42b883">Vue Js</span></h1>
  <div id="s8" class="p-3 rounded-3" style="max-width: 350px; margin: 50px auto; background-color: #234 ">
    <!--    Calculator Result-->
    <div class="w-100 rounded m-1 p-3 text-white text-end lead fw-bold  bg-vue-dark">
      {{ calculatorValue || 0 }}
    </div>
    <!--  Calculator Buttons  -->
    <div class="row g-0 ">
      <div class="col-3 " v-for="n in calculatorElements" :key="n">
        <div class="lead fw-bold text-white text-center m-1 py-3 bg-vue-dark rounded hover-class"
             :class="{'bg-vue-green': ['C','*','/','-','+','%','='].includes(n)}"
             @click="action(n)">
          {{ n }}
        </div>
      </div>

    </div>

  </div>
    <footer class="bg-dark fixed-bottom  text-center text-white">
      <div class="text-center p-3" style="background-color: rgba(0, 0, 0, 0.2);">
        Â© 2022 Copyright:
        <a style="color: #F05454" target="_blank" href="https://mbalhakim.github.io/Portfolio/">Bashar</a>
      </div>
    </footer>
  </div>

</template>

<script>

export default {
  name: 'Calculator-view',
  components: {},
  data() {
    return {
      calculatorValue: '',
      calculatorElements: ['C', '*', '/', '-', 7, 8, 9, '+', 4, 5, 6, '%', 1, 2, 3, '=', 0, '.'],
      operator:null,
      previousCalculatorValue :''

    }
  },
  methods: {
    action(n) {
      //Append Value
      if (!isNaN(n) || n === '.') {
        this.calculatorValue += n + '';
      }
      if (n === 'C') {
        this.calculatorValue = ''
      }
      if (n === '%') {
        this.calculatorValue = this.calculatorValue / 100 + ''
      }
      if (['/','*','-','+'].includes(n)) {
          this.operator = n;
          this.previousCalculatorValue = this.calculatorValue;
          this.calculatorValue = '';
      }
      if(n=== '='){
        this.calculatorValue = eval(
            this.previousCalculatorValue + this.operator + this.calculatorValue
        );
        this.previousCalculatorValue ='';
        this.operator =null;
      }
    },
  }
}
</script>
<style scoped>
.bg-vue-dark {
  background-color: #065471;
}

.hover-class:hover {
  cursor: pointer;
  background: #0A91AB;
}

.bg-vue-green {
  background: #FFC045;
}


</style>